import * as VueRouter from 'vue-router';

const routes = [
    {
        path: "/",
        name: "Home",
        redirect:'/aboutus'
    },
    {
        path: "/compare",
        name: "compare",
        component: () =>
            import(/* webpackChunkName: "compare" */ "../views/page-compare.vue"),
    },
    {
        path: "/motorcycle/:id",
        name: "motorcycle",
        component: () =>
            import(/* webpackChunkName: "compare" */ "../views/page-motorcyle.vue"),
    },
    {
        path: "/contact",
        name: "contact",
        component: () =>
            import(/* webpackChunkName: "compare" */ "../views/page-contact.vue"),
    },
    {
        path: "/aboutus",
        name: "overons",
        component: () =>
            import(/* webpackChunkName: "compare" */ "../views/page-about-us.vue"),
    },
    {
        path: "/*",
        name: "404",
        component: () =>
            import(/* webpackChunkName: "404" */ "../views/page-404.vue"),
    },
];

const router = VueRouter.createRouter({
    history: VueRouter.createWebHistory(),
    routes,
});

export default router;
